import { Map } from 'immutable';
import { StateObject } from 'reducers';
import { NodeInfoType } from 'HOCs';
import { Category } from 'reducers/state.type';

const getSubCategories = (
  path: string,
  subCategories: Map<keyof Category, Category[keyof Category]>[],
) => {
  return subCategories.length > 0
    ? subCategories.map((subCategory, num) => {
        const {
          id,
          title,
          subCategories: childrenCategories,
        }: Category = subCategory.toObject() as Category;

        const jsonSubCategory: NodeInfoType = {
          path: `${path}-subCategories-${num}`,
          id,
          title,
          children:
            childrenCategories.toArray().length > 0
              ? getSubCategories(`${path}-subCategories-${num}`, childrenCategories.toArray())
              : [],
        };

        return jsonSubCategory;
      })
    : [];
};

const getCategories = ({ updateCategories }: StateObject): NodeInfoType[] => {
  return updateCategories
    .get('categories')
    .toArray()
    .map((category, num) => {
      const {
        id,
        title,
        subCategories: childrenCategories,
      }: Category = category.toObject() as Category;

      const jsonCategory: NodeInfoType = {
        path: `categories-${num}`,
        id,
        title,
        children: getSubCategories(`categories-${num}`, childrenCategories.toArray()),
      };

      return jsonCategory;
    });
};

export default getCategories;
