import { select, SelectEffect } from 'redux-saga/effects';
import { isTheSameNameTaskExist, isTheSameNameCategoryExist } from 'selectors';
import { TaskValidateSagaArgumentType, CategoryValidationArgumentType } from './saga.types';

function* categoryValidationSaga({
  payload: { path, title },
}: CategoryValidationArgumentType): Generator<SelectEffect> {
  return yield select(isTheSameNameCategoryExist(title, path));
}

export function* taskValidationSaga({
  payload: { path, title, taskId },
}: TaskValidateSagaArgumentType): Generator<SelectEffect> {
  return yield select(isTheSameNameTaskExist(title, path, taskId));
}

export default categoryValidationSaga;
