import { handleActions } from 'redux-actions';
import { switchMapPicker as pickSituationOnMap } from 'actions';
import { PickSituationState } from './state.type';
import { PickSituationPayload } from './payload.type';

const switchMapPicker = handleActions<PickSituationState, PickSituationPayload>(
  {
    [pickSituationOnMap as unknown as string]: {
      next(state, { payload: { isDisplayed, url } }) {
        if (!url) {
          return { ...state, isDisplayed };
        }
        if (isDisplayed === undefined && url) {
          return { ...state, url };
        }
        return { isDisplayed, url };
      },
    },
  },
  { isDisplayed: true, url: [] },
);

export default switchMapPicker;
