import React, { memo, useCallback } from 'react';
import { useDispatch } from 'react-redux';
import { Task } from 'patterns';
import FORM_FIELDS from 'constants/field-names';
import { CheckboxChangeEvent } from 'antd/lib/checkbox';
import { startEditTaskStatusProcess, switchMapPicker } from 'actions';
import { TaskContainerProps } from './TaskContainer.props';

const TaskContainer: React.FC<TaskContainerProps> = ({
  title,
  isCompleted,
  checkBoxTestId,
  description,
  id,
  categoryId,
  isChosen,
  onSetTaskData,
}) => {
  const dispatch = useDispatch();

  const handleToggleCompleted = useCallback(
    ({ target: { checked } }: CheckboxChangeEvent) => {
      dispatch(startEditTaskStatusProcess({ path: [categoryId, id], isCompleted: checked }));
    },
    [categoryId, dispatch, id],
  );

  const handleChoose = useCallback(() => {
    dispatch(switchMapPicker({ url: [categoryId, id] }));
  }, [categoryId, dispatch, id]);

  const handleOpenEditModal = useCallback(() => {
    onSetTaskData({
      [FORM_FIELDS.TASK_TITLE]: title,
      [FORM_FIELDS.TASK_DESCRIPTION]: description,
      [FORM_FIELDS.TASK_FINISHED]: isCompleted,
      [FORM_FIELDS.TASK_PLACEMENT]: [categoryId],
      taskId: id as string,
    });
  }, [categoryId, description, id, isCompleted, onSetTaskData, title]);

  const handleEnableMap = useCallback(() => {
    dispatch(switchMapPicker({ url: [categoryId, id], isDisplayed: false }));
  }, [categoryId, dispatch, id]);

  return (
    <Task
      title={title}
      isChosen={isChosen}
      isCompleted={isCompleted}
      toggleComplete={handleToggleCompleted}
      onChoose={handleChoose}
      onOpenEditModal={handleOpenEditModal}
      onEnableMap={handleEnableMap}
      checkBoxTestId={checkBoxTestId}
      description={description}
      id={id}
    />
  );
};

export default memo(TaskContainer);
