import React, { memo, useCallback, useRef } from 'react';
import { Modal } from 'antd';
import { CategoryForm, SendDataType } from 'components';
import FORM_FIELDS from 'constants/field-names';
import { EditCategoriesDialogProps } from './EditCategoriesDialog.props';

const EditCategoriesDialog: React.FC<EditCategoriesDialogProps> = ({
  visible,
  onToggle,
  sendData,
  defaultValues,
  children,
  operationTitle,
  validateOnMatch,
  resetAfterSubmit,
  resetAfterCancel,
  formId,
  footerSubmitButton,
}) => {
  const refToCancelSubmitHandler = useRef<() => void>(() => {});

  const handleSendData = useCallback(
    ({ [FORM_FIELDS.CATEGORY_TITLE]: title }: SendDataType) => {
      onToggle();
      sendData({ title });
    },
    [onToggle, sendData],
  );

  const handleCancel = useCallback(() => {
    onToggle();
    if (resetAfterCancel) {
      refToCancelSubmitHandler.current();
    }
  }, [onToggle, resetAfterCancel]);

  return (
    <Modal
      visible={visible}
      title={operationTitle}
      onCancel={handleCancel}
      footer={footerSubmitButton || []}
    >
      <CategoryForm
        key={JSON.stringify(defaultValues)}
        sendData={handleSendData}
        defaultValues={defaultValues}
        resetAfterSubmit={resetAfterSubmit}
        validateOnMatch={validateOnMatch}
        refToCancelSubmit={refToCancelSubmitHandler}
        formId={formId}
      >
        {children}
      </CategoryForm>
    </Modal>
  );
};

export default memo(EditCategoriesDialog);
