import React, { memo, useCallback, ChangeEvent } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { CheckboxChangeEvent } from 'antd/lib/checkbox';
import { showDoneTasks, searchTasks } from 'actions';
import { StateObject } from 'reducers';
import { TasksFilters } from 'components';

const TasksFiltersContainer: React.FC = () => {
  const dispatch = useDispatch();

  const { showDone: isDoneShown, searchKey: searchPattern } = useSelector(
    ({ filterTasks: { showDone, searchKey } }: StateObject) => ({
      showDone,
      searchKey,
    }),
  );

  const handleSwitchDone = useCallback(
    ({ target: { checked } }: CheckboxChangeEvent) => {
      dispatch(showDoneTasks({ showDone: checked }));
    },
    [dispatch],
  );

  const handleChangeSearch = useCallback(
    ({ target: { value } }: ChangeEvent<HTMLInputElement>) => {
      dispatch(searchTasks({ searchKey: value }));
    },
    [dispatch],
  );

  return (
    <TasksFilters
      showDone={!!isDoneShown}
      searchKey={searchPattern || ''}
      onChangeSearch={handleChangeSearch}
      onSwitchDone={handleSwitchDone}
    />
  );
};

export default memo(TasksFiltersContainer);
