import _ from 'lodash';
import { handleActions } from 'redux-actions';
import { addToReDo, deleteFromReDo, clearReDo } from 'actions';
import { ReDoPayload } from './payload.type';
import { RedoState } from './state.type';

const reDo = handleActions<RedoState, ReDoPayload>(
  {
    [addToReDo as unknown as string]: {
      next(state, { payload }) {
        return { redoOperations: _.concat(state.redoOperations, [payload]) };
      },
    },

    [deleteFromReDo as unknown as string]: {
      next(state) {
        return { redoOperations: _.dropRight(state.redoOperations) };
      },
    },

    [clearReDo as unknown as string]: {
      next() {
        return { redoOperations: [] };
      },
    },
  },
  { redoOperations: [] },
);

export default reDo;
