import { FieldErrors } from 'react-hook-form';
import FORM_FIELDS from 'constants/field-names';

export enum NAME_TYPE {
  TASK_TITLE = FORM_FIELDS.TASK_TITLE,
  CATEGORY_TITLE = FORM_FIELDS.CATEGORY_TITLE,
}

export type SendDataType = {
  [key in NAME_TYPE]: string;
};

type ErrorType = FieldErrors<SendDataType>;

type CommonProps = {
  name: NAME_TYPE;
  placeholder: string;
  addButtonName: string;
};

export type AddFormProps = CommonProps & {
  onSubmit: () => void;
  errors: ErrorType;
  onBlurForm: () => void;
};

export type AddFormContainerProps = CommonProps & {
  onAddItem: (data: SendDataType) => void;
  validateOnMatch: (title: string) => Promise<boolean>;
};
