import React, { memo, useContext, useMemo, useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { useParams } from 'react-router-dom';
import { EditTaskContext } from 'contexts';
import { List } from 'antd';
import styled from 'styled-components';
import { getTasksByCategoryId, getChosenTaskUrl, getTasksFilters } from 'selectors';
import { JsonTaskType } from 'reducers/state.type';
import { is, List as ImmutableList } from 'immutable';
import { calculateRatio } from 'actions';
import TaskContainer from '../TaskContainer';
import { StyledListProps } from './TasksListContainer.props';

const StyledList = styled((props: StyledListProps) => <List<JsonTaskType> {...props} />)`
  top: 8px;
  height: calc(100vh - 15vh - 170px);
  overflow: auto;
`;

const customEqualityCheck = (prev: JsonTaskType[], next: JsonTaskType[]): boolean =>
  is(ImmutableList().merge(prev), ImmutableList().merge(next));

const TasksListContainer: React.FC = () => {
  const dispatch = useDispatch();
  const { categoryId } = useParams<{ categoryId: string }>();
  const tasks = useSelector(getTasksByCategoryId(categoryId), customEqualityCheck);
  const [, taskId] = useSelector(getChosenTaskUrl);
  const { onSetTaskData } = useContext(EditTaskContext);
  const { showDone, searchKey } = useSelector(getTasksFilters);

  const filteredTasks = useMemo(
    () =>
      tasks.filter(
        ({ isCompleted, title }) => isCompleted === showDone && title.match(searchKey as string),
      ),
    [showDone, tasks, searchKey],
  );

  useEffect(() => {
    dispatch(
      calculateRatio({
        done: tasks.filter(({ isCompleted }) => isCompleted).length,
        all: tasks.length,
      }),
    );
  }, [dispatch, tasks]);

  return (
    <StyledList
      itemLayout="horizontal"
      dataSource={filteredTasks}
      split={false}
      renderItem={({ id, title, isCompleted, description }) => (
        <List.Item>
          <TaskContainer
            title={title}
            isCompleted={isCompleted}
            id={id}
            description={description}
            checkBoxTestId={`checkbox-test-${id}`}
            categoryId={categoryId}
            isChosen={taskId === id}
            onSetTaskData={onSetTaskData}
          />
        </List.Item>
      )}
    />
  );
};

export default memo(TasksListContainer);
