import React, { memo, createContext, useState, ReactElement, useCallback } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import uniqid from 'uniqid';
import { is, List } from 'immutable';
import { TaskForm, FieldValues } from 'patterns';
import { startEditTaskProcess } from 'actions';
import { getCategories } from 'selectors';
import { sagaExecutor, taskValidationSaga } from 'sagas';
import FORM_FIELDS from 'constants/field-names';
import { NodeInfoType } from 'HOCs';
import { EditCategoryContextType, InitialValues } from './EditTaskContext.props';

const customEqualityCheck = (prev: NodeInfoType[], next: NodeInfoType[]): boolean =>
  is(List().merge(prev), List().merge(next));

export const EditTaskContext = createContext<EditCategoryContextType>({
  onSetTaskData: () => {},
});

const initialValues: InitialValues = {
  [FORM_FIELDS.TASK_TITLE]: '',
  [FORM_FIELDS.TASK_DESCRIPTION]: '',
  [FORM_FIELDS.TASK_FINISHED]: false,
  [FORM_FIELDS.TASK_PLACEMENT]: [''],
  taskId: '',
};

const EditTaskProvider: React.FC<{ children: ReactElement }> = ({ children }) => {
  const [initialTaskData, setInitialTaskData] = useState<InitialValues>(initialValues);

  const [visible, setVisible] = useState<boolean>(false);
  const [key, setKey] = useState<string>('');
  const dispatch = useDispatch();

  const categories = useSelector(getCategories, customEqualityCheck);

  const handleClose = useCallback(() => {
    setVisible(false);
  }, []);

  const handleSetTaskData = useCallback((data: InitialValues) => {
    setKey(uniqid());
    setInitialTaskData((previous: InitialValues) => ({ ...previous, ...data }));
    setVisible(true);
  }, []);

  const handleSendData = useCallback(
    ({
      [FORM_FIELDS.TASK_TITLE]: title,
      [FORM_FIELDS.TASK_DESCRIPTION]: description,
      [FORM_FIELDS.TASK_FINISHED]: isCompleted,
      [FORM_FIELDS.TASK_PLACEMENT]: [categoryId],
    }: FieldValues) => {
      dispatch(
        startEditTaskProcess({
          path: [initialTaskData[FORM_FIELDS.TASK_PLACEMENT][0], initialTaskData.taskId],
          newPath: [categoryId, initialTaskData.taskId],
          title,
          description,
          isCompleted,
        }),
      );
      setVisible(false);
    },
    [dispatch, initialTaskData],
  );

  const validateOnMatch = useCallback(
    (taskTitle: string, [categoryId]: [string]): Promise<boolean> =>
      sagaExecutor(taskValidationSaga, {
        payload: {
          path: categoryId,
          title: taskTitle,
          taskId: initialTaskData.taskId,
        },
      }),
    [initialTaskData.taskId],
  );

  return (
    <EditTaskContext.Provider value={{ onSetTaskData: handleSetTaskData }}>
      <TaskForm
        key={key}
        nodesInfo={categories}
        defaultValues={initialTaskData}
        visible={visible}
        onClose={handleClose}
        sendData={handleSendData}
        validateOnMatch={validateOnMatch}
      />
      {children}
    </EditTaskContext.Provider>
  );
};

export default memo(EditTaskProvider);
