import React, { memo, useMemo } from 'react';
import styled from 'styled-components';
import generateTree from 'HOCs';
import { CategoryItemProps, CategoryTreeProps } from './CategoriesTree.props';

const StyledText = styled.p`
  line-height: 15px;
`;

const CategoryItem: React.FC<CategoryItemProps> = ({ title }) => <StyledText>{title}</StyledText>;

const Categories = generateTree(CategoryItem);

const CategoriesTree: React.FC<CategoryTreeProps> = ({ onChange, value, nodesInfo }) => {
  const expandedKeys = useMemo(
    () =>
      value.length
        ? value[0].split('-').reduce((acc: string[], current, index, arr) => {
            if (index === 0) {
              return acc.concat(current);
            }
            if (index > 0 && index < arr.length - 1) {
              return acc.concat(`${acc[index - 1]}-${current}`);
            }
            return acc;
          }, [])
        : [],
    [value],
  );

  return (
    <Categories
      onSelectNode={onChange}
      selectedKeys={value}
      nodesInfo={nodesInfo}
      expandedKeys={expandedKeys}
    />
  );
};

export default memo(CategoriesTree);
