import React, { memo, forwardRef, ForwardRefRenderFunction } from 'react';
import styled from 'styled-components';
import { RefTextAreaType } from 'types';

const StyledTextArea = styled.textarea`
  height: 150px !important;
`;

const UncontrolledTextArea: ForwardRefRenderFunction<RefTextAreaType> = (
  { ...props },
  inputRef,
) => <StyledTextArea className="ant-input" ref={inputRef as RefTextAreaType} {...props} />;

const WrappedTextArea = forwardRef<RefTextAreaType>(UncontrolledTextArea);

export default memo(WrappedTextArea);
