import { StateObject } from 'reducers';
import { Map } from 'immutable';
import { Task, Category } from 'reducers/state.type';
import { UnionType } from '../reducers/payload.type';

export const getTask =
  (path: UnionType[], pathParam?: string) =>
  ({ updateMappedTasks }: StateObject): Task =>
    path.length
      ? (
          updateMappedTasks.getIn([...path, ...(pathParam ? [pathParam] : [])]) || Map({})
        ).toObject()
      : {};

export const getCategory =
  (path: UnionType[], pathParam?: number) =>
  ({ updateCategories }: StateObject): Category =>
    updateCategories.getIn([...path, pathParam]).toObject();
