import { takeEvery, put, select, ForkEffect } from 'redux-saga/effects';
import { getLastSubCategoryPathParamAndId, PathParamAndId } from 'selectors';
import { addSubCategory, deleteCategory, addToUnDo, clearReDo } from 'actions';
import { UpdateCategoriesSagaArgumentType, UpdateTasksGenerator } from './saga.types';

export function* addSubCategorySaga({
  payload: { path, title },
}: UpdateCategoriesSagaArgumentType): UpdateTasksGenerator {
  yield put(clearReDo());
  yield put(addSubCategory({ path, title }));
  const { pathParam, id } = (yield select(
    getLastSubCategoryPathParamAndId(path),
  )) as PathParamAndId;
  yield put(
    addToUnDo({
      undoOperation: deleteCategory,
      redoOperation: addSubCategory,
      obj: {
        id,
        path,
        pathParam,
        title,
        subCategories: [],
      },
    }),
  );
}

function* addSubCategoryWatcher(): Generator<ForkEffect> {
  yield takeEvery('START_ADD_SUB_CATEGORY_PROCESS', addSubCategorySaga);
}

export default addSubCategoryWatcher;
