import React, { memo, forwardRef, ForwardRefRenderFunction } from 'react';
import 'antd/dist/antd.css';
import { Button as AntButton, Typography } from 'antd';
import styled from 'styled-components';
import { addFormInputTestId, addFormTestId } from 'constants/test-ids';

import { RefInputType } from 'types';
import { AddFormProps } from './AddForm.props';

const { Text: AntText } = Typography;

const Form = styled.form`
  display: flex;
  position: sticky;
  top: 0px;
`;

const StyledInput = styled.input`
  display: block;
`;

const Text = styled(AntText)`
  position: absolute;
  left: 50px;
  top: 40px;
`;

const AddForm: ForwardRefRenderFunction<RefInputType, AddFormProps> = (
  { onSubmit, errors, name, onBlurForm, placeholder, addButtonName, ...props },
  inputRef,
) => (
  <Form autoComplete="off" onSubmit={onSubmit} onBlur={onBlurForm} data-testid={addFormTestId}>
    <StyledInput
      {...props}
      name={name as unknown as string}
      className="ant-input"
      ref={inputRef as RefInputType}
      placeholder={placeholder}
      data-testid={addFormInputTestId}
    />
    <AntButton htmlType="submit" type="primary">
      {addButtonName}
    </AntButton>
    {errors[name] && <Text type="danger">{errors[name]?.message}</Text>}
  </Form>
);

const WrappedAddform = forwardRef<RefInputType, AddFormProps>(AddForm);

export default memo(WrappedAddform);
