import { takeEvery, put, select, ForkEffect } from 'redux-saga/effects';
import { editTaskLocation, addToUnDo, switchMapPicker, clearReDo } from 'actions';
import { getTask } from 'selectors';
import { Task } from 'reducers/state.type';
import { UpdateTasksGenerator, UpdateTasksSagaArgumentType } from './saga.types';

export function* editTaskLocationSaga({
  payload: { path, location },
}: UpdateTasksSagaArgumentType): UpdateTasksGenerator {
  yield put(clearReDo());
  const task = yield select(getTask(path));
  yield put(editTaskLocation({ path, location }));
  yield put(switchMapPicker({ isDisplayed: true, path: [] }));
  yield put(
    addToUnDo({
      undoOperation: editTaskLocation.bind(null, {
        path,
        location: (task as Task).location.toObject(),
      }),
      redoOperation: editTaskLocation.bind(null, { path, location }),
      obj: {},
    }),
  );
}

function* editTaskLocationWatcher(): Generator<ForkEffect> {
  yield takeEvery('START_EDIT_TASK_LOCATION', editTaskLocationSaga);
}

export default editTaskLocationWatcher;
