import { takeEvery, put, select, ForkEffect } from 'redux-saga/effects';
import { deleteFromReDo, addToUnDo } from 'actions';
import { getRedoOperationConfig } from 'selectors';
import { OperationConfig } from 'selectors/types';
import { OperationGenerator } from './saga.types';

export function* redoOperationSaga(): OperationGenerator {
  const { undoOperation, redoOperation, obj } = (yield select(
    getRedoOperationConfig,
  )) as OperationConfig;
  yield put(redoOperation(obj));
  yield put(addToUnDo({ undoOperation, redoOperation, obj }));
  yield put(deleteFromReDo());
}

function* redoOperationWatcher(): Generator<ForkEffect> {
  yield takeEvery('START_REDO_PROCESS', redoOperationSaga);
}

export default redoOperationWatcher;
