import React, { memo, useCallback, useMemo } from 'react';
import { useForm } from 'react-hook-form';
import AddForm from './AddForm';
import { AddFormContainerProps } from './AddForm.props';

const AddFormContainer: React.FC<AddFormContainerProps> = ({
  name,
  placeholder,
  onAddItem,
  validateOnMatch,
  addButtonName,
}) => {
  const {
    handleSubmit,
    reset,
    formState: { errors },
    setError,
    register,
  } = useForm();

  const submit = useCallback(
    async ({ [name]: title }) => {
      const isError = await validateOnMatch(title);
      if (isError) {
        setError(name as unknown as string, {
          type: 'notMatch',
          message: 'An item with such name exists',
        });
      } else {
        onAddItem({ [name]: title });
        reset({ [name]: '' });
      }
    },
    [name, onAddItem, reset, setError, validateOnMatch],
  );

  const handleBlurForm = useCallback(() => {
    setTimeout(() => reset({ [name]: '' }), 500);
  }, [name, reset]);

  const onSubmit = useMemo(() => handleSubmit(submit), [handleSubmit, submit]);

  return (
    <AddForm
      onSubmit={onSubmit}
      onBlurForm={handleBlurForm}
      errors={errors}
      placeholder={placeholder}
      addButtonName={addButtonName}
      {...{
        ...register(name as unknown as string, {
          required: 'This is required',
        }),
        name,
      }}
    />
  );
};

export default memo(AddFormContainer);
