import { Map } from 'immutable';
import { StateObject } from 'reducers';
import { UnionType } from 'reducers/payload.type';
import { JsonTaskType, TaskImmutableMap, Task, TasksFilterState } from 'reducers/state.type';

const getCompletedTasksRatio = ({ calculateTaskRatio: { done, all } }: StateObject): number =>
  all ? (done / all) * 100 : 0;

export const getChosenTaskUrl = ({ switchMapPicker: { url } }: StateObject): UnionType[] => url;

export const getVisibilityOfTasks = ({ switchMapPicker: { isDisplayed } }: StateObject): boolean =>
  !!isDisplayed;

export const getTasksByCategoryId =
  (categoryId: string) =>
  ({ updateMappedTasks }: StateObject): JsonTaskType[] =>
    Object.values((updateMappedTasks.get(categoryId) || Map({})).toObject()).map(
      (taskMap: TaskImmutableMap) => {
        const task: Task = taskMap.toObject() as Task;
        return { ...task, location: task.location.toObject() };
      },
    );

export const getTasksFilters = ({
  filterTasks: { showDone, searchKey },
}: StateObject): TasksFilterState => ({
  showDone,
  searchKey,
});

export default getCompletedTasksRatio;
