import { ReactElement } from 'react';

export enum DIRECTION {
  ROW = 'row',
  COLUMN = 'column',
}

export enum JUSTIFY_CONTENT_TYPES {
  FLEX_START = 'flex-start',
  FLEX_END = 'flex-end',
}

export type WrapperProps = {
  flexDirection: DIRECTION;
};

export type ExtendedWrapperProps = WrapperProps & {
  justifyContent?: JUSTIFY_CONTENT_TYPES;
};

export type FormItemProps = WrapperProps & {
  labelName: string;
  children: ReactElement;
  htmlFor?: string;
};
