import React, { memo } from 'react';
import styled from 'styled-components';
import { Checkbox, Input } from 'antd';
import { TasksFilterProps } from './TaskFilters.props';

const Wrapper = styled.div`
  margin-top: 40px;
  display: flex;
  justify-content: center;
  position: sticky;
  top: 75px;
`;

const TasksFilters: React.FC<TasksFilterProps> = ({
  showDone,
  searchKey,
  onSwitchDone,
  onChangeSearch,
}) => (
  <Wrapper>
    <Checkbox defaultChecked={showDone} onChange={onSwitchDone}>
      Show done
    </Checkbox>
    <Input
      placeholder="search"
      style={{ width: 200 }}
      defaultValue={searchKey}
      onChange={onChangeSearch}
    />
  </Wrapper>
);

export default memo(TasksFilters);
