import React, { memo } from 'react';
import styled from 'styled-components';
import { Checkbox, Button } from 'antd';
import { StyledEditIcon } from '../icons';
import { LabelProps, TaskHeaderProps } from './TaskHeader.props';
import 'antd/dist/antd.css';

const StyledLabel = styled.span`
  padding: 0 10px 0;
  cursor: pointer;
  color: ${({ isChosen }: LabelProps) => (isChosen ? 'red' : 'black')};
`;

const StyledButton = styled(Button)`
  left: 10px;
`;

const TaskHeader: React.FC<TaskHeaderProps> = ({
  title,
  isChosen,
  isCompleted,
  toggleComplete,
  onChoose,
  onOpenEditModal,
  onEnableMap,
  checkBoxTestId,
}) => (
  <div>
    <Checkbox data-testid={checkBoxTestId} checked={isCompleted} onChange={toggleComplete} />
    <StyledLabel isChosen={isChosen} onClick={onChoose}>
      {title}
    </StyledLabel>
    <StyledEditIcon onClick={onOpenEditModal} role={`edit-icon-${title}`} />
    <StyledButton type="primary" onClick={onEnableMap}>
      Pick place
    </StyledButton>
  </div>
);

export default memo(TaskHeader);
