import React, { ReactElement } from 'react';
import { createStore, applyMiddleware } from 'redux';
import { Provider } from 'react-redux';
import { all } from 'redux-saga/effects';
import { HashRouter, Route } from 'react-router-dom';
import { composeWithDevTools } from 'redux-devtools-extension';
import { Layout } from 'antd';
import styled from 'styled-components';
import {
  CategoriesTreeContainer,
  AddCategoryContainer,
  HeaderContainer,
  WorldMapContainer,
  ContentContainer,
} from 'containers';
import { EditCategoryProvider, EditTaskProvider } from 'contexts';
import background from 'assets/images/background.jpg';
import rootReducer from './reducers';
import {
  addCategoryWatcher,
  addSubCategoryWatcher,
  addTaskWatcher,
  deleteCategoryWatcher,
  deleteTaskWatcher,
  editCategoryWatcher,
  editTaskWatcher,
  editTaskStatusWatcher,
  redoOperationWatcher,
  undoOperationWatcher,
  sagaMiddleware,
  editTaskLocationSaga,
} from './sagas';
import './App.css';

const { Sider, Header, Content: ContentWrapper } = Layout;

const StyledSider = styled(Sider)`
  background: transparent;
  padding: 30px;
  & > div > div {
    display: flex;
    flex-direction: column;
  }
`;

const Main = styled(Layout)`
  background: transparent;
`;

const StyledHeader = styled(Header)`
  background-color: transparent;
  height: 15%;
  border-radius: 10px;
  overflow: hidden;
`;

const MainLayout = styled(Layout)`
  height: 100vh;
  background: url(${background}) no-repeat;
  background-size: 100% 100%;
`;

const StyledContentWrapper = styled(ContentWrapper)`
  overflow-x: visible;
`;

const store = createStore(rootReducer, composeWithDevTools(applyMiddleware(sagaMiddleware)));

function* rootSaga() {
  yield all([
    addCategoryWatcher(),
    addSubCategoryWatcher(),
    addTaskWatcher(),
    deleteCategoryWatcher(),
    deleteTaskWatcher(),
    editCategoryWatcher(),
    editTaskWatcher(),
    editTaskStatusWatcher(),
    redoOperationWatcher(),
    undoOperationWatcher(),
    editTaskLocationSaga(),
  ]);
}

sagaMiddleware.run(rootSaga);

export const Wrapper = ({ children }: { children: ReactElement }): ReactElement => (
  <Provider store={store}>
    <HashRouter>{children}</HashRouter>
  </Provider>
);

function App(): ReactElement {
  return (
    <Wrapper>
      <MainLayout>
        <StyledHeader>
          <HeaderContainer />
        </StyledHeader>
        <Main hasSider>
          <StyledSider width="30%">
            <AddCategoryContainer />
            <EditCategoryProvider>
              <CategoriesTreeContainer />
            </EditCategoryProvider>
          </StyledSider>
          <StyledContentWrapper>
            <WorldMapContainer />
            <EditTaskProvider>
              <Route path="/:categoryId">
                <ContentContainer />
              </Route>
            </EditTaskProvider>
          </StyledContentWrapper>
        </Main>
      </MainLayout>
    </Wrapper>
  );
}

export default App;
