import React, { memo, forwardRef, ForwardRefRenderFunction } from 'react';
import { Typography } from 'antd';
import { categoryFormTestId, categoryFormInputTestId } from 'constants/test-ids';

import { RefInputType } from 'types';
import { CategoryFormProps } from './CategoryForm.props';

const { Text } = Typography;

const CategoryForm: ForwardRefRenderFunction<RefInputType, CategoryFormProps> = (
  { onSubmit, errors: { title: titleError }, children, formId, ...props },
  inputRef,
) => (
  <form id={formId} data-testid={categoryFormTestId} autoComplete="off" onSubmit={onSubmit}>
    {children || (
      <>
        <input
          className="ant-input"
          data-testid={categoryFormInputTestId}
          ref={inputRef as RefInputType}
          {...props}
        />
        {titleError && <Text type="danger">{titleError.message}</Text>}
      </>
    )}
  </form>
);

const WrappedCategoryForm = forwardRef<RefInputType, CategoryFormProps>(CategoryForm);

export default memo(WrappedCategoryForm);
