import { takeEvery, put, select, ForkEffect } from 'redux-saga/effects';
import { editTask, addToUnDo, clearReDo } from 'actions';
import { getTask } from 'selectors';
import { Task } from 'reducers/state.type';
import { UpdateTasksGenerator, UpdateTasksSagaArgumentType } from './saga.types';

export function* editTaskSaga({
  payload: { path, newPath, title, description, isCompleted },
}: UpdateTasksSagaArgumentType): UpdateTasksGenerator {
  yield put(clearReDo());
  const task = yield select(getTask(path));
  yield put(
    editTask({
      path,
      newPath,
      title,
      description,
      isCompleted,
    }),
  );
  yield put(
    addToUnDo({
      undoOperation: editTask.bind(null, {
        path: newPath,
        newPath: path,
        title: (task as Task).title,
        description: (task as Task).description,
        isCompleted: (task as Task).isCompleted,
      }),
      redoOperation: editTask.bind(null, {
        path,
        newPath,
        title,
        description,
        isCompleted,
      }),
      obj: {},
    }),
  );
}

function* editTaskWatcher(): Generator<ForkEffect> {
  yield takeEvery('START_EDIT_TASK_PROCESS', editTaskSaga);
}

export default editTaskWatcher;
