import React, { memo, useCallback } from 'react';
import { useDispatch } from 'react-redux';
import { useParams } from 'react-router-dom';
import { startAddTaskProcess } from 'actions';
import { AddForm, NAME_TYPE } from 'patterns';
import { sagaExecutor, taskValidationSaga } from 'sagas';

const AddTaskContainer: React.FC = () => {
  const dispatch = useDispatch();
  const { categoryId } = useParams<{ categoryId: string }>();

  const handleAddTask = useCallback(
    ({ [NAME_TYPE.TASK_TITLE]: taskTitle }) => {
      dispatch(startAddTaskProcess({ title: taskTitle, path: [categoryId] }));
    },
    [dispatch, categoryId],
  );

  const validateOnMatch = useCallback(
    (taskTitle: string): Promise<boolean> => {
      return sagaExecutor(taskValidationSaga, {
        payload: { path: categoryId, title: taskTitle, taskId: '' },
      });
    },
    [categoryId],
  );

  return (
    <AddForm
      name={NAME_TYPE.TASK_TITLE}
      placeholder="Enter task title"
      onAddItem={handleAddTask}
      validateOnMatch={validateOnMatch}
      addButtonName="Add task"
    />
  );
};

export default memo(AddTaskContainer);
