import { handleActions } from 'redux-actions';
import { Map, List } from 'immutable';
import uniqid from 'uniqid';
import { addCategory, addSubCategory, deleteCategory, editCategory, insertCategory } from 'actions';
import { UpdateCategoriesPayload } from './payload.type';
import { CategoriesFullImmutableState, CategoryImmutableMap, Category } from './state.type';
import initialState from './initialState';

const updateCategories = handleActions<CategoriesFullImmutableState, UpdateCategoriesPayload>(
  {
    [addCategory as unknown as string]: {
      next(state, { payload: { title } }) {
        return state.update('categories', (categories) =>
          (categories as List<CategoryImmutableMap>).push(
            Map<keyof Category, Category[keyof Category]>({
              id: uniqid(),
              title,
              subCategories: List<CategoryImmutableMap>([]),
            }),
          ),
        );
      },
    },

    [addSubCategory as unknown as string]: {
      next(state, { payload: { path, title } }) {
        return state.updateIn(path, (subCategories) =>
          (subCategories as List<CategoryImmutableMap>).push(
            Map<keyof Category, Category[keyof Category]>({
              id: uniqid(),
              title,
              subCategories: List([]),
            }),
          ),
        );
      },
    },

    [deleteCategory as unknown as string]: {
      next(state, { payload: { path, pathParam } }) {
        return state.deleteIn([...path, pathParam]);
      },
    },

    [editCategory as unknown as string]: {
      next(state, { payload: { path, pathParam, title } }) {
        return state.updateIn([...path, pathParam], (category) => category.set('title', title));
      },
    },

    [insertCategory as unknown as string]: {
      next(state, { payload: { id, path, pathParam, title, subCategories } }) {
        return state.updateIn(path, (categories) =>
          categories.insert(
            pathParam,
            Map({
              id,
              title,
              subCategories,
            }),
          ),
        );
      },
    },
  },
  Map().merge(initialState) as CategoriesFullImmutableState,
);

export default updateCategories;
