import React, { memo, forwardRef, ForwardRefRenderFunction } from 'react';
import { RefInputType } from 'types';

const UncontrolledInput: ForwardRefRenderFunction<RefInputType> = ({ ...props }, inputRef) => (
  <input className="ant-input" ref={inputRef as RefInputType} {...props} />
);

const WrappedInput = forwardRef<RefInputType>(UncontrolledInput);

export default memo(WrappedInput);
