import { handleActions } from 'redux-actions';
import { showDoneTasks, searchTasks } from 'actions';
import { TasksFilterPayload } from './payload.type';
import { TasksFilterState } from './state.type';

const filterTasks = handleActions<TasksFilterState, TasksFilterPayload>(
  {
    [showDoneTasks as unknown as string]: {
      next(state, { payload: { showDone } }) {
        return { ...state, showDone };
      },
    },

    [searchTasks as unknown as string]: {
      next(state, { payload: { searchKey } }) {
        return { ...state, searchKey };
      },
    },
  },
  { showDone: Boolean(true), searchKey: '' },
);

export default filterTasks;
