import {
  combineReducers,
  Reducer,
  StateFromReducersMapObject,
  ActionFromReducersMapObject,
} from 'redux';
import { ReduxCompatibleReducer } from 'redux-actions';
import updateCategories from './updateCategories';
import updateMappedTasks from './updateMappedTasks';
import filterTasks from './filterTasks';
import calculateTaskRatio from './calculateTaskRatio';
import reDo from './reDo';
import unDo from './unDo';
import switchMapPicker from './switchMapPicker';
import {
  CategoriesFullImmutableState,
  TasksFilterState,
  TaskRatioState,
  RedoState,
  UnDoState,
  PickSituationState,
  ImmutableMappedTasksState,
} from './state.type';
import {
  UpdateCategoriesPayload,
  TasksFilterPayload,
  TaskRatioPayload,
  ReDoPayload,
  UnDoPayload,
  PickSituationPayload,
  UpdateMappedTasksPayload,
} from './payload.type';

export type ReducerObject = {
  updateCategories: ReduxCompatibleReducer<CategoriesFullImmutableState, UpdateCategoriesPayload>;
  updateMappedTasks: ReduxCompatibleReducer<ImmutableMappedTasksState, UpdateMappedTasksPayload>;
  filterTasks: ReduxCompatibleReducer<TasksFilterState, TasksFilterPayload>;
  calculateTaskRatio: ReduxCompatibleReducer<TaskRatioState, TaskRatioPayload>;
  reDo: ReduxCompatibleReducer<RedoState, ReDoPayload>;
  unDo: ReduxCompatibleReducer<UnDoState, UnDoPayload>;
  switchMapPicker: ReduxCompatibleReducer<PickSituationState, PickSituationPayload>;
};

type MainReducer = Reducer<
  StateFromReducersMapObject<ReducerObject>,
  ActionFromReducersMapObject<ReducerObject>
>;

export type StateObject = StateFromReducersMapObject<ReducerObject>;

const reducer: MainReducer = combineReducers({
  updateCategories,
  updateMappedTasks,
  filterTasks,
  calculateTaskRatio,
  reDo,
  unDo,
  switchMapPicker,
});

export default reducer;
