import { PathConfig } from 'types';

export const getPathParam = (path: string): PathConfig => ({
  savingPath: path.split('-'),
  param: path.split('-')[path.split('-').length - 1],
});

export const getSavingPath = (path: string): (string | number)[] => {
  const { savingPath } = getPathParam(path);
  savingPath.pop();
  return savingPath;
};
