import { handleActions } from 'redux-actions';
import { Map } from 'immutable';
import { editTaskStatus, addTask, editTask, deleteTask, editTaskLocation } from 'actions';
import { initialMappedTasksState } from './initialState';
import { UpdateMappedTasksPayload } from './payload.type';
import { ImmutableMappedTasksState, TaskImmutableMap } from './state.type';

const updateMappedTasks = handleActions<ImmutableMappedTasksState, UpdateMappedTasksPayload>(
  {
    [editTaskStatus as unknown as string]: {
      next(
        state,
        {
          payload: {
            path: [categoryId, taskId],
            isCompleted,
          },
        },
      ) {
        return state.setIn([categoryId, taskId, 'isCompleted'], isCompleted);
      },
    },
    [addTask as unknown as string]: {
      next(
        state,
        {
          payload: {
            id,
            path: [categoryId],
            title,
            location,
          },
        },
      ) {
        return (
          state.get(categoryId as string) ? state : state.set(categoryId as string, Map({}))
        ).update(categoryId as string, (tasksMap) =>
          tasksMap.set(
            id as string,
            Map({
              id,
              title,
              location: location || Map({}),
              isCompleted: false,
              description: '',
            }) as TaskImmutableMap,
          ),
        );
      },
    },
    [editTask as unknown as string]: {
      next(state, { payload: { path, newPath, title, description, isCompleted } }) {
        if (newPath) {
          const [categoryId, taskId] = newPath;
          const location = state.getIn([...path, 'location']);
          return state.deleteIn(path).update(categoryId as string, (tasksMap) =>
            tasksMap.set(
              taskId as string,
              Map({
                id: taskId,
                title,
                location,
                isCompleted,
                description,
              }) as TaskImmutableMap,
            ),
          );
        }
        return state.updateIn(path, (task) =>
          task.set('title', title).set('description', description).set('isCompleted', isCompleted),
        );
      },
    },
    [deleteTask as unknown as string]: {
      next(
        state,
        {
          payload: {
            path: [categoryId],
            pathParam: taskId,
          },
        },
      ) {
        return state.deleteIn([categoryId, taskId]);
      },
    },
    [editTaskLocation as unknown as string]: {
      next(
        state,
        {
          payload: {
            path: [categoryId, taskId],
            location,
          },
        },
      ) {
        return state.setIn(
          [categoryId, taskId, 'location'],
          Map(location as Record<string, unknown>),
        );
      },
    },
  },
  Map().merge(initialMappedTasksState) as ImmutableMappedTasksState,
);

export default updateMappedTasks;
