import React, { memo } from 'react';
import styled from 'styled-components';
import {
  WrapperProps,
  FormItemProps,
  ExtendedWrapperProps,
  JUSTIFY_CONTENT_TYPES,
  DIRECTION,
} from './FormItem.props';

const LabelWrapper = styled.div`
  flex: 0.3;
  display: flex;
  padding-right: 20px;
  & > label {
    text-align: end;
    font-weight: bold;
  }
`;

const Wrapper = styled.div.attrs<WrapperProps>(({ flexDirection }) => ({
  justifyContent:
    flexDirection === DIRECTION.COLUMN
      ? JUSTIFY_CONTENT_TYPES.FLEX_START
      : JUSTIFY_CONTENT_TYPES.FLEX_END,
  flexDirection,
}))<ExtendedWrapperProps>`
  display: flex;
  padding: 10px 0;
  flex-direction: ${({ flexDirection }) => flexDirection};
  & > ${LabelWrapper} {
    justify-content: ${({ justifyContent }) => justifyContent};
  }
`;

const FieldWrapper = styled.div`
  flex: 0.7;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
`;

const FormItem: React.FC<FormItemProps> = ({ labelName, children, htmlFor, flexDirection }) => (
  <Wrapper flexDirection={flexDirection}>
    <LabelWrapper>
      <label htmlFor={htmlFor}>{`${labelName}`}</label>
    </LabelWrapper>
    <FieldWrapper>{children}</FieldWrapper>
  </Wrapper>
);

export default memo(FormItem);
