import React, { memo, useEffect, useCallback } from 'react';
import { useForm } from 'react-hook-form';
import FORM_FIELDS from 'constants/field-names';
import CategoryForm from './CategoryForm';
import { CategoryFormContainerProps, SendDataType } from './CategoryForm.props';

export const requiredMessage = 'Title can`t be empty';

const CategoryFormContainer: React.FC<CategoryFormContainerProps> = ({
  sendData,
  defaultValues,
  children,
  resetAfterSubmit = false,
  validateOnMatch,
  refToCancelSubmit,
  formId,
}) => {
  const {
    handleSubmit,
    register,
    reset,
    setError,
    formState: { errors },
  } = useForm<SendDataType>({ defaultValues });

  const submitData = useCallback(
    async ({ [FORM_FIELDS.CATEGORY_TITLE]: title }: SendDataType) => {
      const isError = (await validateOnMatch?.(title)) || false;
      if (isError) {
        setError(FORM_FIELDS.CATEGORY_TITLE, {
          type: 'notMatch',
          message: 'An item with such name exists',
        });
      } else {
        sendData({ [FORM_FIELDS.CATEGORY_TITLE]: title });
        if (resetAfterSubmit) {
          reset({ ...defaultValues });
        }
      }
    },
    [sendData, setError, validateOnMatch, defaultValues, reset, resetAfterSubmit],
  );

  const cancelSubmit = useCallback(() => {
    reset({ ...defaultValues });
  }, [reset, defaultValues]);

  useEffect(() => {
    if (refToCancelSubmit) {
      // eslint-disable-next-line no-param-reassign
      refToCancelSubmit.current = cancelSubmit;
    }
  }, [cancelSubmit, refToCancelSubmit]);

  return (
    <CategoryForm
      onSubmit={handleSubmit(submitData)}
      errors={errors}
      formId={formId}
      {...register(FORM_FIELDS.CATEGORY_TITLE, { required: requiredMessage })}
    >
      {children}
    </CategoryForm>
  );
};

export default memo(CategoryFormContainer);
