import { takeEvery, put, select, ForkEffect } from 'redux-saga/effects';
import { deleteFromUnDo, addToReDo } from 'actions';
import { getUndoOperationConfig } from 'selectors';
import { OperationConfig } from 'selectors/types';
import { OperationGenerator } from './saga.types';

export function* undoOperationSaga(): OperationGenerator {
  const { undoOperation, redoOperation, obj } = (yield select(
    getUndoOperationConfig,
  )) as OperationConfig;
  yield put(undoOperation(obj));
  yield put(addToReDo({ undoOperation, redoOperation, obj }));
  yield put(deleteFromUnDo());
}

function* undoOperationWatcher(): Generator<ForkEffect> {
  yield takeEvery('START_UNDO_PROCESS', undoOperationSaga);
}

export default undoOperationWatcher;
