import React, { memo } from 'react';
import styled from 'styled-components';
import { Link } from 'react-router-dom';
import { StyledPlusIcon, StyledEditIcon, StyledDeleteIcon } from '../icons';
import CategoryProps from './Category.props';

const StyledLink = styled(Link)`
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue';
  font-size: 14px;
  text-decoration: none;
  color: #40a9ff;
  font-weight: 400;
`;

const Category: React.FC<CategoryProps> = ({
  path,
  title,
  onOpenEditModal,
  onOpenAddModal,
  onOpenDeleteModal,
  editIconTestId,
  addIconTestId,
  deleteIconTestId,
}) => (
  <div>
    <span>
      <StyledLink to={`/${path}`}>{title}</StyledLink>
      <StyledEditIcon data-testid={editIconTestId} onClick={onOpenEditModal} />
      <StyledPlusIcon data-testid={addIconTestId} onClick={onOpenAddModal} />
      <StyledDeleteIcon data-testid={deleteIconTestId} onClick={onOpenDeleteModal} />
    </span>
  </div>
);

export default memo(Category);
