import React, { memo, useCallback } from 'react';
import { useDispatch } from 'react-redux';
import { startAddCategoryProcess } from 'actions';
import { AddForm, NAME_TYPE } from 'patterns';
import { sagaExecutor, categoryValidationSaga } from 'sagas';

const AddCategoryContainer: React.FC = () => {
  const dispatch = useDispatch();

  const handleAddCategory = useCallback(
    ({ [NAME_TYPE.CATEGORY_TITLE]: categoryTitle }) => {
      dispatch(startAddCategoryProcess({ title: categoryTitle }));
    },
    [dispatch],
  );

  const validateOnMatch = useCallback((categoryTitle: string): Promise<boolean> => {
    return sagaExecutor(categoryValidationSaga, {
      payload: { path: ['categories'], title: categoryTitle },
    });
  }, []);

  return (
    <AddForm
      name={NAME_TYPE.CATEGORY_TITLE}
      placeholder="Enter name"
      onAddItem={handleAddCategory}
      validateOnMatch={validateOnMatch}
      addButtonName="Add"
    />
  );
};

export default memo(AddCategoryContainer);
