import { takeEvery, put, ForkEffect } from 'redux-saga/effects';
import { editTaskStatus, addToUnDo, clearReDo } from 'actions';
import { UpdateTasksGenerator, UpdateTasksSagaArgumentType } from './saga.types';

export function* editTaskStatusSaga({
  payload: { path, pathParam, isCompleted },
}: UpdateTasksSagaArgumentType): UpdateTasksGenerator {
  yield put(clearReDo());
  yield put(editTaskStatus({ path, pathParam, isCompleted }));
  yield put(
    addToUnDo({
      undoOperation: editTaskStatus.bind(null, { path, pathParam, isCompleted: !isCompleted }),
      redoOperation: editTaskStatus.bind(null, { path, pathParam, isCompleted }),
      obj: {},
    }),
  );
}

function* editTaskStatusWatcher(): Generator<ForkEffect> {
  yield takeEvery('START_EDIT_TASK_STATUS_PROCESS', editTaskStatusSaga);
}

export default editTaskStatusWatcher;
