import { takeEvery, put, select, ForkEffect } from 'redux-saga/effects';
import { editCategory, addToUnDo, clearReDo } from 'actions';
import { getCategory } from 'selectors';
import { Category } from 'reducers/state.type';
import { UpdateTasksGenerator, UpdateCategoriesSagaArgumentType } from './saga.types';

export function* editCategorySaga({
  payload: { path, pathParam, title },
}: UpdateCategoriesSagaArgumentType): UpdateTasksGenerator {
  yield put(clearReDo());
  const category = yield select(getCategory(path, pathParam));
  yield put(editCategory({ path, pathParam, title }));
  yield put(
    addToUnDo({
      undoOperation: editCategory.bind(null, {
        path,
        pathParam,
        title: (category as Category).title,
      }),
      redoOperation: editCategory.bind(null, { path, pathParam, title }),
      obj: {},
    }),
  );
}

function* editCategoryWatcher(): Generator<ForkEffect> {
  yield takeEvery('START_EDIT_CATEGORY_PROCESS', editCategorySaga);
}

export default editCategoryWatcher;
