import React, { ReactElement } from 'react';
import 'antd/dist/antd.css';
import { Tree as AntTree } from 'antd';
import styled from 'styled-components';
import { TreeNodeProps, NodeInfoType, TreeNodeTitleProps, TreeProps } from './tree.props';

const { TreeNode } = AntTree;

export const StyledTreeNode = styled(TreeNode)``;

const StyledTree = styled(AntTree)`
  margin-top: 20px !important;
  width: 90%;
  align-self: center;
  max-height: 85%;
  overflow: auto;
`;

const generateTree = (TreeNodeTitle: React.FC<TreeNodeTitleProps>): React.FC<TreeProps> => {
  const getTreeNode = (
    nodeInfo: NodeInfoType,
    getChildNodes?: (arg: NodeInfoType[]) => ReactElement<TreeNodeProps, typeof StyledTreeNode>[],
  ) => (
    <StyledTreeNode
      key={nodeInfo.id}
      title={<TreeNodeTitle id={nodeInfo.id} path={nodeInfo.path} title={nodeInfo.title} />}
    >
      {getChildNodes && getChildNodes(nodeInfo.children)}
    </StyledTreeNode>
  );

  const getTreeNodes = (nodesInfo: NodeInfoType[]) =>
    nodesInfo.map((nodeInfo) => {
      if (nodeInfo.children.length) {
        return getTreeNode(nodeInfo, getTreeNodes);
      }
      return getTreeNode(nodeInfo);
    });

  const Tree: React.FC<TreeProps> = ({
    onSelectNode,
    nodesInfo,
    selectedKeys,
    expandedKeys,
    ...props
  }) => (
    <StyledTree
      draggable
      onSelect={onSelectNode}
      defaultExpandedKeys={expandedKeys}
      {...(selectedKeys && { selectedKeys })}
      {...props}
    >
      {getTreeNodes(nodesInfo)}
    </StyledTree>
  );

  return Tree;
};

export default generateTree;
