import React, { memo, useCallback } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { Header } from 'patterns';
import { startUnDoProcess, startReDoProcess } from 'actions';
import { getCompletedTasksRatio, getOperationsAmount } from 'selectors';

const HeaderContainer: React.FC = () => {
  const dispatch = useDispatch();

  const handleUndo = useCallback(() => dispatch(startUnDoProcess()), [dispatch]);

  const handleRedo = useCallback(() => dispatch(startReDoProcess()), [dispatch]);

  const percentage = useSelector(getCompletedTasksRatio);

  const { unDoAmount, reDoAmount } = useSelector(getOperationsAmount);

  return (
    <Header
      percentage={percentage}
      onUndo={handleUndo}
      onRedo={handleRedo}
      isUnDoDisabled={!unDoAmount}
      isReDoDisabled={!reDoAmount}
    />
  );
};

export default memo(HeaderContainer);
