import React, { memo, useCallback } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { startEditTaskLocationProcess } from 'actions';
import { WorldMap } from 'patterns';
import { getChosenTaskUrl, getTask } from 'selectors';
import { LatLngExpression, LeafletMouseEvent } from 'leaflet';
import { JsonTaskType } from 'reducers/state.type';
import { is, Map } from 'immutable';

const customEqualityCheck = (prev: JsonTaskType, next: JsonTaskType): boolean =>
  is(Map().merge(prev), Map().merge(next));

const WorldMapContainer: React.FC = () => {
  const dispatch = useDispatch();
  const chosenTaskUrl = useSelector(getChosenTaskUrl);
  const chosenTask = useSelector(getTask(chosenTaskUrl), customEqualityCheck);
  const taskLocation: LatLngExpression =
    chosenTask?.location?.toObject() as unknown as LatLngExpression;

  const handleClickLocation = useCallback(
    ({ latlng }: LeafletMouseEvent) => {
      if (chosenTaskUrl.length) {
        dispatch(startEditTaskLocationProcess({ path: chosenTaskUrl, location: latlng }));
      }
    },
    [chosenTaskUrl, dispatch],
  );

  return (
    <WorldMap
      coords={taskLocation && Object.keys(taskLocation).length ? taskLocation : undefined}
      onClick={handleClickLocation}
    />
  );
};

export default memo(WorldMapContainer);
