import React, { memo } from 'react';
import { Map, TileLayer, Marker } from 'react-leaflet';
import styled from 'styled-components';
import { WorldMapProps, WrapperProps } from './WorldMap.props';

const accessToken =
  'pk.eyJ1Ijoic2FuZGVyYXMiLCJhIjoiY2s0bGJ2aW44Mjg5NjNubzJ2NXlvMmk1eiJ9.U4UYcWGp-W0S9CeW5EFcpg';
const id = 'mapbox/streets-v11';

const StyledMapContainer = styled(Map)<WrapperProps>`
  position: absolute;
  width: 70%;
  height: calc(100vh - 15%);
  overflow: hidden;
  top: 15%;
  z-index: 1;
`;

const WorldMap: React.FC<WorldMapProps> = ({ coords, onClick }) => (
  <StyledMapContainer
    data-testid="map"
    center={coords || [52.093, 23.695]}
    zoom={12}
    onClick={onClick}
  >
    <TileLayer
      url={`https://api.mapbox.com/styles/v1/${id}/tiles/{z}/{x}/{y}?access_token=${accessToken}`}
      attribution="Map data &copy; <a href='https://www.openstreetmap.org/'>OpenStreetMap</a> contributors, <a href='https://creativecommons.org/licenses/by-sa/2.0/'>CC-BY-SA</a>, Imagery  <a href='https://www.mapbox.com/'>Mapbox</a>"
    />
    {coords && (
      <div data-testid="marker">
        <Marker position={coords} />
      </div>
    )}
  </StyledMapContainer>
);

export default memo(WorldMap);
