import React, { memo } from 'react';
import 'antd/dist/antd.css';
import { Button, Icon } from 'antd';
import { UndoRedoButtonsProps } from './UndoRedoButtons.props';

const UndoRedoButtons: React.FC<UndoRedoButtonsProps> = ({
  onRedo,
  onUndo,
  isReDoDisabled,
  isUnDoDisabled,
}) => (
  <Button.Group size="small">
    <Button type="primary" disabled={isUnDoDisabled} onClick={onUndo}>
      <Icon type="left" />
      Backward
    </Button>
    <Button type="primary" disabled={isReDoDisabled} onClick={onRedo}>
      Forward
      <Icon type="right" />
    </Button>
  </Button.Group>
);

export default memo(UndoRedoButtons);
