import _ from 'lodash';
import { handleActions } from 'redux-actions';
import { addToUnDo, deleteFromUnDo } from 'actions';
import { UnDoPayload } from './payload.type';
import { UnDoState } from './state.type';

const unDo = handleActions<UnDoState, UnDoPayload>(
  {
    [addToUnDo as unknown as string]: {
      next(state, { payload }) {
        return { undoOperations: _.concat(state.undoOperations, [payload]) };
      },
    },

    [deleteFromUnDo as unknown as string]: {
      next(state) {
        return { undoOperations: _.dropRight(state.undoOperations) };
      },
    },
  },
  { undoOperations: [] },
);

export default unDo;
