import React, { ReactElement } from 'react';
import { Controller, useFormContext } from 'react-hook-form';
import { Typography } from 'antd';
import {
  ControlledFieldComponentProps,
  ControlledFormFieldProps,
  UncontrolledFormFieldProps,
} from './FormField.props';

const { Text } = Typography;

export const ControlledFormField = <
  T extends ControlledFieldComponentProps,
  R extends ControlledFormFieldProps<T>,
>({
  rules,
  as: Component,
  fieldName,
  valuePropName = 'value',
  ...props
}: R): ReactElement => {
  const { control } = useFormContext();

  return (
    <Controller
      control={control}
      name={fieldName}
      rules={rules}
      render={({ field: { onChange, value }, fieldState: { error } }) => (
        <>
          <Component onChange={onChange} {...{ [valuePropName]: value, ...props }} />
          {error && <Text type="danger">{error.message}</Text>}
        </>
      )}
    />
  );
};

export const UncontrolledFormField: React.FC<UncontrolledFormFieldProps> = ({
  rules,
  as: Component,
  fieldName,
  ...props
}) => {
  const {
    register,
    formState: { errors },
  } = useFormContext();

  return (
    <>
      <Component {...register(fieldName, rules)} {...props} />
      {errors[fieldName] && <Text type="danger">{errors[fieldName].message}</Text>}
    </>
  );
};
