import { takeEvery, put, select, ForkEffect } from 'redux-saga/effects';
import { addCategory, deleteCategory, addToUnDo, clearReDo, SAGA_ACTION_TITLES } from 'actions';
import { getLastCategoryPathAndId, PathAndId } from 'selectors';
import { UpdateCategoriesSagaArgumentType, UpdateTasksGenerator } from './saga.types';

export function* addCategorySaga({
  payload: { title },
}: UpdateCategoriesSagaArgumentType): UpdateTasksGenerator {
  yield put(clearReDo());

  yield put(addCategory({ title }));
  const { path, id } = (yield select(getLastCategoryPathAndId)) as PathAndId;
  yield put(
    addToUnDo({
      undoOperation: deleteCategory,
      redoOperation: addCategory,
      obj: {
        id,
        path: [(path as string).split('-')[0]],
        pathParam: (path as string).split('-')[1],
        title,
        subCategories: [],
      },
    }),
  );
}

function* addCategoryWatcher(): Generator<ForkEffect> {
  yield takeEvery(SAGA_ACTION_TITLES.START_ADD_CATEGORY_PROCESS, addCategorySaga);
}

export default addCategoryWatcher;
