import React, { memo, NamedExoticComponent, useCallback } from 'react';
import 'antd/dist/antd.css';
import { Row, Col, Modal } from 'antd';
import FORM_FIELDS from 'constants/field-names';
import { FormProvider, useForm } from 'react-hook-form';
import {
  UncontrolledInput,
  UncontrolledTextArea,
  ControlledFormField,
  UncontrolledFormField,
  FormItem,
  DIRECTION,
} from 'components';

import { CategoriesTree, TaskCompleted } from './components';
import { TaskFormProps, FieldValues } from './TaskForm.props';
import FIELD_IDS from './constants';

export const defaultSaveButtonTitle = 'Save';
export const defaultCancelButtonTitle = 'Cancel';
export const labelNames = {
  [FORM_FIELDS.TASK_PLACEMENT]: 'Task placement:',
  [FORM_FIELDS.TASK_TITLE]: 'Task title:',
  [FORM_FIELDS.TASK_FINISHED]: 'Completed:',
  [FORM_FIELDS.TASK_DESCRIPTION]: 'Task description:',
};
export const errorMessages = {
  [FORM_FIELDS.TASK_TITLE]: 'Task title can`t be empty',
  [FORM_FIELDS.TASK_DESCRIPTION]: 'Task description can`t be empty',
};

const TaskForm: React.FC<TaskFormProps> = ({
  nodesInfo,
  defaultValues,
  visible,
  onClose,
  sendData,
  saveButtonTitle = defaultSaveButtonTitle,
  cancelButtonTitle = defaultCancelButtonTitle,
  validateOnMatch,
}) => {
  const { handleSubmit, setError, ...methods } = useForm({ defaultValues });

  const sendDataHandler = useCallback(
    async (values: FieldValues) => {
      const isError = await validateOnMatch(
        values[FORM_FIELDS.TASK_TITLE],
        values[FORM_FIELDS.TASK_PLACEMENT],
      );
      if (isError) {
        setError(FORM_FIELDS.TASK_TITLE, {
          type: 'notMatch',
          message: 'An item with such name exists',
        });
      } else {
        sendData(values);
      }
    },
    [sendData, setError, validateOnMatch],
  );

  return (
    <FormProvider {...{ handleSubmit, setError, ...methods }}>
      <Modal
        title="Edit task"
        visible={visible}
        onCancel={onClose}
        onOk={handleSubmit(sendDataHandler)}
        okText={saveButtonTitle}
        cancelButtonProps={{ children: cancelButtonTitle }}
      >
        <Row>
          <Col span={9}>
            <FormItem
              flexDirection={DIRECTION.COLUMN}
              labelName={labelNames[FORM_FIELDS.TASK_PLACEMENT]}
            >
              <ControlledFormField
                as={CategoriesTree as NamedExoticComponent}
                fieldName={FORM_FIELDS.TASK_PLACEMENT}
                nodesInfo={nodesInfo}
              />
            </FormItem>
          </Col>
          <Col span={15}>
            <Row>
              <FormItem
                flexDirection={DIRECTION.ROW}
                htmlFor={FIELD_IDS.TITLE}
                labelName={labelNames[FORM_FIELDS.TASK_TITLE]}
              >
                <UncontrolledFormField
                  rules={{ required: errorMessages[FORM_FIELDS.TASK_TITLE] }}
                  as={UncontrolledInput}
                  fieldName={FORM_FIELDS.TASK_TITLE}
                  id={FIELD_IDS.TITLE}
                />
              </FormItem>
            </Row>
            <Row>
              <FormItem
                flexDirection={DIRECTION.ROW}
                labelName={labelNames[FORM_FIELDS.TASK_FINISHED]}
                htmlFor={FIELD_IDS.IS_COMPLETED}
              >
                <ControlledFormField
                  as={TaskCompleted as NamedExoticComponent}
                  fieldName={FORM_FIELDS.TASK_FINISHED}
                  valuePropName="checked"
                  id={FIELD_IDS.IS_COMPLETED}
                />
              </FormItem>
            </Row>
            <Row>
              <FormItem
                flexDirection={DIRECTION.ROW}
                labelName={labelNames[FORM_FIELDS.TASK_DESCRIPTION]}
                htmlFor={FIELD_IDS.DESCRIPTION}
              >
                <UncontrolledFormField
                  rules={{ required: errorMessages[FORM_FIELDS.TASK_DESCRIPTION] }}
                  as={UncontrolledTextArea}
                  fieldName={FORM_FIELDS.TASK_DESCRIPTION}
                  id={FIELD_IDS.DESCRIPTION}
                />
              </FormItem>
            </Row>
          </Col>
        </Row>
      </Modal>
    </FormProvider>
  );
};

export default memo(TaskForm);
