import { handleActions, ReduxCompatibleReducer } from 'redux-actions';
import { calculateRatio } from 'actions';
import { TaskRatioState } from './state.type';
import { TaskRatioPayload } from './payload.type';

const calculateTaskRatio: ReduxCompatibleReducer<TaskRatioState, TaskRatioPayload> = handleActions(
  {
    [calculateRatio as unknown as string]: {
      next(state, { payload: { done, all } }) {
        return { done, all };
      },
    },
  },
  { done: 0, all: 0 },
);

export default calculateTaskRatio;
