import { takeEvery, put, ForkEffect, call } from 'redux-saga/effects';
import uniqid from 'uniqid';
import { addTask, deleteTask, addToUnDo, clearReDo } from 'actions';
import { UpdateTasksGenerator, UpdateTasksSagaArgumentType } from './saga.types';

export function* addTaskSaga({
  payload: { path, title },
}: UpdateTasksSagaArgumentType): UpdateTasksGenerator {
  yield put(clearReDo());

  const id = yield call(uniqid);
  yield put(addTask({ id, path, title }));
  yield put(
    addToUnDo({
      undoOperation: deleteTask,
      redoOperation: addTask,
      obj: {
        id,
        path,
        pathParam: id,
        title,
      },
    }),
  );
}

function* addTaskWatcher(): Generator<ForkEffect> {
  yield takeEvery('START_ADD_TASK_PROCESS', addTaskSaga);
}

export default addTaskWatcher;
