import React, { memo, useContext } from 'react';
import { Category } from 'components';
import { EditCategoryContext, OPERATION_TYPE } from 'contexts';
import CategoryProps from 'components/Category/Category.props';

const CategoryContainer: React.FC<Pick<CategoryProps, 'path' | 'title' | 'id'>> = ({
  id,
  path,
  title,
}) => {
  const { onSetCategoryData } = useContext(EditCategoryContext);

  return (
    <Category
      id={id}
      path={id}
      title={title}
      onOpenAddModal={() => onSetCategoryData({ path, title, operationType: OPERATION_TYPE.ADD })}
      onOpenEditModal={() => onSetCategoryData({ path, title, operationType: OPERATION_TYPE.EDIT })}
      onOpenDeleteModal={() =>
        onSetCategoryData({ path, title, operationType: OPERATION_TYPE.DELETE })
      }
      editIconTestId={`${path}-edit`}
      addIconTestId={`${path}-add`}
      deleteIconTestId={`${path}-delete`}
    />
  );
};

export default memo(CategoryContainer);
