import { StateObject } from 'reducers';
import { UnionType } from '../reducers/payload.type';

export type PathAndId = {
  path: string;
  id: string;
};

export const getLastCategoryPathAndId = ({ updateCategories }: StateObject): PathAndId => {
  const lastIndex = updateCategories.get('categories').toArray().length - 1;
  return {
    path: `categories-${lastIndex}`,
    id: updateCategories.getIn(['categories', lastIndex, 'id']),
  };
};

export type PathParamAndId = {
  pathParam: number;
  id: string;
};

export const getLastSubCategoryPathParamAndId =
  (path: UnionType[]) =>
  ({ updateCategories }: StateObject): PathParamAndId => {
    const lastSubCategoryIndex = updateCategories.getIn(path).toArray().length - 1;
    return {
      pathParam: lastSubCategoryIndex,
      id: updateCategories.getIn([...path, lastSubCategoryIndex, 'id']),
    };
  };
