import _ from 'lodash';
import { StateObject } from 'reducers';
import { OperationConfig, OperationsAmountConfig } from './types';

export const getUndoOperationConfig = ({ unDo }: StateObject): OperationConfig =>
  _.get(unDo, `undoOperations[${unDo.undoOperations.length - 1}]`);

export const getRedoOperationConfig = ({ reDo }: StateObject): OperationConfig =>
  _.get(reDo, `redoOperations[${reDo.redoOperations.length - 1}]`);

export const getOperationsAmount = ({ unDo, reDo }: StateObject): OperationsAmountConfig => ({
  unDoAmount: _.get(unDo, 'undoOperations').length,
  reDoAmount: _.get(reDo, 'redoOperations').length,
});
