import React, { memo } from 'react';
import { useSelector } from 'react-redux';
import styled from 'styled-components';
import generateTree, { StyledTreeNode } from 'HOCs';
import { getCategories } from 'selectors';
import CategoryContainer from '../CategoryContainer';

const Tree = generateTree(CategoryContainer);

const StyledTree = styled(Tree)`
  & ${StyledTreeNode} {
    .ant-tree-node-content-wrapper {
      background-color: rgb(41, 189, 226);
      border-radius: 10px;
      color: white;
    }

    .ant-tree-node-content-wrapper a {
      color: white;
    }

    .ant-tree-node-content-wrapper a:hover {
      color: red;
    }

    .ant-tree-node-selected {
      background-color: transparent !important;
      color: red;
    }

    .ant-tree-node-selected a {
      background-color: transparent;
      color: red;
    }
  }
`;

const CategoriesTreeContainer = () => {
  const categories = useSelector(getCategories);

  return <StyledTree nodesInfo={categories} />;
};

export default memo(CategoriesTreeContainer);
