import { takeEvery, put, select, ForkEffect } from 'redux-saga/effects';
import { deleteCategory, addToUnDo, insertCategory, clearReDo } from 'actions';
import { getCategory } from 'selectors';
import { Category } from 'reducers/state.type';
import { UpdateCategoriesSagaArgumentType, UpdateTasksGenerator } from './saga.types';

export function* deleteCategorySaga({
  payload: { path, pathParam },
}: UpdateCategoriesSagaArgumentType): UpdateTasksGenerator {
  yield put(clearReDo());
  const category = yield select(getCategory(path, pathParam));
  yield put(deleteCategory({ path, pathParam }));
  yield put(
    addToUnDo({
      undoOperation: insertCategory,
      redoOperation: deleteCategory,
      obj: { ...(category as Category), path, pathParam },
    }),
  );
}

function* deleteCategoryWatcher(): Generator<ForkEffect> {
  yield takeEvery('START_DELETE_CATEGORY_PROCESS', deleteCategorySaga);
}

export default deleteCategoryWatcher;
