import { Map } from 'immutable';
import { StateObject } from 'reducers';
import { UnionType } from '../reducers/payload.type';
import { CategoryImmutableMap } from '../reducers/state.type';

const getEntitiesTitles = ({ updateCategories }: StateObject, path: UnionType[]) =>
  updateCategories
    .getIn(path)
    .map((entity: CategoryImmutableMap) => entity.get('title'))
    .toArray();

const isTheSameNameCategoryExist =
  (title: string, path: UnionType[]) =>
  (state: StateObject): boolean =>
    getEntitiesTitles(state, path).includes(title);

export const isTheSameNameTaskExist =
  (title: string, path: string, taskId: string) =>
  ({ updateMappedTasks }: StateObject): boolean => {
    return (updateMappedTasks.get(path) || Map({})).reduce(
      (reduction, value) =>
        !reduction ? value?.get('title') === title && value?.get('id') !== taskId : true,
      Boolean(false),
    );
  };

export default isTheSameNameCategoryExist;
