import React, { memo, createContext, useState, useCallback, ReactElement } from 'react';
import { useDispatch } from 'react-redux';
import { Button } from 'antd';
import { EditCategoriesDialog } from 'patterns';
import FORM_FIELDS from 'constants/field-names';
import { SendDataType } from 'components';
import {
  startAddSubCategoryProcess,
  startDeleteCategoryProcess,
  startEditCategoryProcess,
} from 'actions';
import { sagaExecutor, categoryValidationSaga } from 'sagas';
import { getPathParam, getSavingPath } from 'utils';
import {
  EdittedCategoryData,
  OPERATION_TYPE,
  EditCategoryContextType,
} from './EditCategoryContext.props';

export const EditCategoryContext = createContext<EditCategoryContextType>({
  onSetCategoryData: () => {},
});

const OPERATION_TITLES = {
  [OPERATION_TYPE.ADD]: 'Add new subcategory',
  [OPERATION_TYPE.EDIT]: 'Edit category',
  [OPERATION_TYPE.DELETE]: 'Delete category',
};

const RESET_AFTER_SUBMIT_VALUES = {
  [OPERATION_TYPE.ADD]: true,
  [OPERATION_TYPE.EDIT]: false,
  [OPERATION_TYPE.DELETE]: false,
};

const EditCategoryProvider: React.FC<{ children: ReactElement }> = ({ children }) => {
  const [{ path, title, operationType }, setCategoryData] = useState<EdittedCategoryData>({
    path: '',
    title: '',
    operationType: OPERATION_TYPE.ADD,
  });

  const [visible, setVisible] = useState<boolean>(false);
  const dispatch = useDispatch();

  const handleClose = useCallback(() => setVisible(false), []);

  const handleSetCategoryData = useCallback(
    ({ title: categoryTitle, operationType: type, ...other }: EdittedCategoryData) => {
      setCategoryData({
        title: type === OPERATION_TYPE.ADD ? '' : categoryTitle,
        operationType: type,
        ...other,
      });
      setVisible(true);
    },
    [],
  );

  const handleSendData = useCallback(
    ({ [FORM_FIELDS.CATEGORY_TITLE]: categoryTitle }: SendDataType) => {
      switch (operationType) {
        case OPERATION_TYPE.ADD:
          dispatch(
            startAddSubCategoryProcess({
              path: `${path}-subCategories`.split('-'),
              title: categoryTitle,
            }),
          );
          break;

        case OPERATION_TYPE.EDIT:
          dispatch(
            startEditCategoryProcess({
              path: getSavingPath(path),
              pathParam: getPathParam(path).param,
              title: categoryTitle,
            }),
          );
          break;

        case OPERATION_TYPE.DELETE:
          dispatch(
            startDeleteCategoryProcess({
              path: getSavingPath(path),
              pathParam: getPathParam(path).param,
            }),
          );
          break;

        default:
          break;
      }
    },
    [dispatch, operationType, path],
  );

  const validateOnMatch = useCallback(
    (entityTitle: string): Promise<boolean> =>
      operationType === OPERATION_TYPE.ADD || operationType === OPERATION_TYPE.EDIT
        ? sagaExecutor(categoryValidationSaga, {
            payload: {
              path:
                operationType === OPERATION_TYPE.ADD
                  ? `${path}-subCategories`.split('-')
                  : getSavingPath(path),
              title: entityTitle,
            },
          })
        : Promise.resolve(false),
    [path, operationType],
  );

  return (
    <EditCategoryContext.Provider value={{ onSetCategoryData: handleSetCategoryData }}>
      <EditCategoriesDialog
        visible={visible}
        onToggle={handleClose}
        sendData={handleSendData}
        defaultValues={{ [FORM_FIELDS.CATEGORY_TITLE]: title } as SendDataType}
        operationTitle={OPERATION_TITLES[operationType]}
        validateOnMatch={validateOnMatch}
        formId="edit-category"
        resetAfterSubmit={RESET_AFTER_SUBMIT_VALUES[operationType]}
        resetAfterCancel
        {...(operationType === OPERATION_TYPE.DELETE
          ? {
              footerSubmitButton: (
                <Button type="primary" htmlType="submit" form="edit-category">
                  Delete
                </Button>
              ),
            }
          : null)}
      >
        {operationType === OPERATION_TYPE.DELETE ? (
          <>
            <input type="hidden" name={FORM_FIELDS.CATEGORY_TITLE} />
            <p>{`Delete the category: ${title}`}</p>
          </>
        ) : undefined}
      </EditCategoriesDialog>
      {children}
    </EditCategoryContext.Provider>
  );
};

export default memo(EditCategoryProvider);
