import { takeEvery, put, select, ForkEffect } from 'redux-saga/effects';
import { addTask, deleteTask, addToUnDo } from 'actions';
import { getTask } from 'selectors';
import { Task } from 'reducers/state.type';
import { UpdateTasksGenerator, UpdateTasksSagaArgumentType } from './saga.types';

export function* deleteTaskSaga({
  payload: { path, pathParam },
}: UpdateTasksSagaArgumentType): UpdateTasksGenerator {
  const task = yield select(getTask(path, pathParam as string));
  yield put(deleteTask({ path, pathParam }));
  yield put(
    addToUnDo({
      undoOperation: addTask,
      redoOperation: deleteTask,
      obj: {
        id: (task as Task).id,
        title: (task as Task).title,
        pathParam,
        path,
        location: (task as Task).location,
      },
    }),
  );
}

function* deleteTaskWatcher(): Generator<ForkEffect> {
  yield takeEvery('START_DELETE_TASK_PROCESS', deleteTaskSaga);
}

export default deleteTaskWatcher;
